'use client'

import { useState } from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { motion, AnimatePresence } from "framer-motion";

const navLinks = [
  { label: "About", href: "#about" },
  { label: "Companies", href: "#companies" },
  { label: "Events", href: "#events" },
  { label: "Get Involved", href: "#involved" },
];

export default function NavBar() {
  const [open, setOpen] = useState(false);
  return (
    <nav className="w-full bg-gradient-to-r from-[#6c2eb7] to-[#23235b] shadow-md rounded-2xl mt-4 px-6 py-3 flex items-center justify-between font-sans relative z-40">
      <span className="text-2xl font-extrabold text-white tracking-tight">Braselton Tech</span>
      <ul className="hidden md:flex gap-8 text-base font-semibold text-white">
        {navLinks.map(link => (
          <li key={link.href}><Link href={link.href} className="hover:text-purple-200 transition">{link.label}</Link></li>
        ))}
      </ul>
      <div className="hidden md:block">
        <Button className="bg-white text-[#6c2eb7] font-bold px-6 py-2 rounded-xl shadow-lg hover:bg-purple-100 transition">Contact</Button>
      </div>
      {/* Hamburger for mobile */}
      <button className="md:hidden flex flex-col gap-1" onClick={() => setOpen(o => !o)} aria-label="Open menu">
        <span className="w-7 h-1 bg-white rounded transition-all" />
        <span className="w-7 h-1 bg-white rounded transition-all" />
        <span className="w-7 h-1 bg-white rounded transition-all" />
      </button>
      <AnimatePresence>
        {open && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="absolute top-16 left-0 w-full bg-gradient-to-r from-[#6c2eb7] to-[#23235b] rounded-b-2xl shadow-lg flex flex-col items-center gap-6 py-8 md:hidden"
          >
            {navLinks.map(link => (
              <Link key={link.href} href={link.href} className="text-lg font-semibold text-white hover:text-purple-200 transition" onClick={() => setOpen(false)}>{link.label}</Link>
            ))}
            <Button className="bg-white text-[#6c2eb7] font-bold px-6 py-2 rounded-xl shadow-lg hover:bg-purple-100 transition">Contact</Button>
          </motion.div>
        )}
      </AnimatePresence>
    </nav>
  );
} 